/** @format */

.header {
  height: 70px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
  color: #fff;
  margin-bottom:0px ;
}
.header__logo:hover,
.header__selectLocation:hover,
.header__flag:hover,
.header__option:hover,
.header__searchInput:hover {
  cursor: pointer;
  border: 1px solid #fff;
}
.header__selectLocation div:hover {
  border: none;
}
.header__logo {
  width: 100px;
  padding: 25px 0 0px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 5px;
  
}

.header__nav-left div {
  display: flex;
}
.LocationOnOutlinedIcon {
  color: white;
  position: relative;
  top: 23px;
}

.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}

.header__searchOption select {
  padding: 8px 10px 8px 20px;
  border-radius: 4px 0 0 4px;
  outline: none;
  scroll-behavior: smooth;
  background-color: rgb(243, 238, 238);
  color: #000;
  max-width: 65px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.header__searchOption select:hover,
.ArrowDropDownIcon-wrapper:hover {
  cursor: pointer;
  background-color: rgb(218, 211, 211);
}
.search_ArrowDropDownIcon {
  color: #131921;

  padding-top: 4px;
  margin-left: -30px;
}
.ArrowDropDownIcon-wrapper {
  height: 31px;
  background-color: rgb(243, 238, 238);
}
.header__searchInput {
  height: 11px;
  padding: 10px;
  border: none;
  width: 100%;
  outline: none;
}
.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  color: #000;
  background-color: #eebe7f;
  cursor: pointer;
  border-radius: 0 4px 4px 0;
}
.header__search:hover .header__searchOption {
  border-radius: 4px 0 0 4px;
  border: 2px solid #df9b42;
  border-right: none;
}
.header__search:hover .header__searchIcon {
  border-radius: 0 4px 4px 0;
  border: 2px solid #df9b42;
  border-left: none;
}
.header__search:hover .header__searchInput {
  border: 3px solid #df9b42;
  border-right: none;
  border-left: none;
}

.header__searchIcon:hover {
  background-color: #df9b42;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}
.header__option:nth-child(1) {
  width: 100px;
}
.header__nav-right,
.header__nav-left {
  display: flex;
  justify-content: space-evenly;
}
.header__flag img {
  padding-left: 15px;
  height: 18px;
  width: 30px;
}
.ArrowDropDownIcon {
  position: relative;
  top: 6px;
}
.header__language {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 65px;
  background-color: rgb(248, 248, 248);
  border-radius: 5px;
}
.header__nav-left .header__option {
  margin: auto 15px auto 0;
}
.header__optionLineOne {
  font-size: 10px;
  text-align: left;
}
.header__optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}
.header__optionReturns{
  font-size: 12px;
}
.header__optionOrders{
  font-size: 13px;
   font-weight: 800;
}
.header__userAccount {
  color: #000;
  background-color: white;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  position: fixed;
  right: 63px;
  top: 70px;
  width: 440px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: none;
  text-align: left;
}
@media (max-width: 1016px) {
  .header__userAccount {
    right: 16vw;
  }
}
@media (max-width: 616px) {
  .header__userAccount {
    right: 10vw;
  }
}
@media (max-width: 496px) {
  .header__userAccount {
    right: 5vw;
  }
}
.header__userAccount::after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: white;
  position: absolute;
  top: -24px;
  right: 22.5%;
  transform: rotateZ(45deg) translateY(20px);
}
.header__userAccount::before {
  position: absolute;
  top: -30px;
  left: 46%;
  content: "";
  color: black;
  opacity: 0;
  background-color: white;
  /* opacity: 1; */
  width: 30%;
  padding-bottom: 30px;
}
.header__option_popup:hover .header__userAccount {
  display: flex;
}
.header__userSignedOut {
  display: contents;
  justify-content: center;
  /* background-color: #f8bc6c; */
}
.header__userAccount button {
  background-color: #f8bc6c;
  margin-top: 15px;
  margin-bottom: 10px;
  padding: 10px 80px;
  border: 1px solid;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
  color: black;
}
.header__userAccount p {
  margin-bottom: 10px;
  font-size: 12px;
}
.header__userAccount p .link {
  text-decoration: none !important;
}
.header__accountList {
  background-color: white;
  border: 1px solid grey;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  list-style: none;
}
.header__accountList div {
  margin: 10px 0px 40px;
  width: 40%;
}
.header__accountList h3 {
  margin: 10px 0;
}

.header__accountList1,
.header__accountList2 {
  line-height: 2em;
  font-size: 12px;
  list-style: none;
}
.header__accountList1 li:hover,
.header__accountList2 li:hover {
  text-decoration: underline;
  color: orange;
}
.header__accountList2_signout {
  font-weight: 700;
  text-align: center;
  background-color: #f8bc6c;
  border-radius: 2px;
}
.AddShoppingCartTwoToneIcon {
  padding-right: 40px;
  margin-top: -10px;
}

.header__itemsNumber::after {
  content: "Cart";
  position: relative;
  left: 4px;
  top: 14px;
  color: #fff;
}
.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
}
.header__clearlink {
  text-decoration: none;
}
.header__itemsNumber {
    position: relative;
    left: 9.9%;
    top: 0px;
    width: 12px;
    height: 16px;
    font-weight: 700;
    padding-bottom: 4px;
    color: #f08804;
    font-size: 15px;
    border-radius: 9px;
    background-color: #131921;
}

.hide {
  display: none;
}

/* ////////// Bottom Navigation//////////// */
.headerBottom {
  height: 20px;
  background-color: #0F1111;
  color: #FFFFFF;
  /* padding: 4px; */
      padding-bottom: 30px;
    padding-top: 1px;
    font-size: 13px;
    line-height: 20px;
    font-family: Arial,sans-serif;
}

.headerBottom__nav {
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.headerBottom__nav > li {
  font-size: 14px;
  padding: 1px 1px 2px;
}
.headerBottom__nav > li:hover {
  border: 1px solid #fff;
}
.headerBottom_hamburger {
    display: flex;
    flex-direction: column;
    gap: 2px;
    padding-top: 4px;
}